/* Progress screen */

@keyframes progress {
    0% {
        background-position: -180px center;
    }
    25% {
        background-position: -120px center;
    }
    50% {
        background-position: -60px center;
    }
    75% {
        background-position: 0px center;
    }
    100% {
        background-position: 60px center;
    }
}
  
@-webkit-keyframes progress {
    0% {
      background-position: -180px center;
    }
    25% {
      background-position: -120px center;
    }
    50% {
      background-position: -60px center;
    }
    75% {
      background-position: 0px center;
    }
    100% {
      background-position: 60px center;
    }
}
  
.tq-ui-progress-screen {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: #fff;
    opacity: .9;
    border-radius: 5px;

    h1 {
        margin-top: -50px;
        font-family: arial;
        font-style: normal;
        color: #87929c;
        font-size: 35px;
    }

    &__progress {
        background-image: url('../../src/resources/images/progress.png');
        animation: progress 2s infinite step-end;
        -webkit-animation: progress 2s infinite step-end;
        position: absolute;
        width: 190px;
        height: 10px;
        background-repeat: no-repeat;
        background-repeat: no-repeat;
    }
}
