.tq-ui-legend-panel, .tq-ui-general-panel {
    width: 100%;
    height: calc(100% - 34px);
}

.tq-ui-legend-panel_body, .tq-ui-general-panel_body {
    width: 100%;
    height: calc(100% - 5px);
    overflow-x: hidden;
    overflow-y: auto;
    margin-top: 5px;
}

.tq-ui-legend-panel_body_legends {
    display: flex;
    flex-wrap: wrap; 
    padding: 15px;
    width: 100%;
}

.tq-ui-legend-panel_body_legends_legend {
    margin: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    width: 50px;

    .tq-ui-legend-panel_body_legends_legend_img {
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 5px;
        img {
            height: 100%;
        }
    }

    label {
        text-align: center;
        font-size: 10px;
        word-wrap: break-word;
        max-width: 75px;
        flex-grow: 1;
    }
}

.tq-ui-export-group, .tq-ui-opt-group {
    display: flex;
}