/* Switcher */

.tq-ui-switch-buttons {
    display: flex;
    border: 1px solid #949494;
    border-radius: 5px;
    background-color: #949494;
    overflow: hidden;

    button, .tq-ui-switch-buttons__button {
        border: 0px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding: 0px 10px;

        white-space: nowrap;
        text-overflow: ellipsis;
        
        outline: 0px;
        overflow: hidden;
        flex-grow: 1;
        border-left: 1px solid #949494;
        background-color: #dadada;

        &:hover {
            background-color: #ececec;
        }

        &:active {
            background-color: whitesmoke;
            box-shadow: inset 0px 0px 2px 3px white;
        }

        &:disabled {
            cursor: not-allowed;
            box-shadow: inset -1px 1px 6px 0px #767676;
            background-color: #c9c9c9;
        }

        &:first-child {
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
            border-left: 0px;
        }

        &:last-child {
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
        }
    }
}

/* Vertical Switcher */

.tq-ui-switch-buttons-vertical {
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 4px -2px black;
    border: 0.5px solid #949494;
    background-color: #949494;
    border-radius: 5px;
    width: 30px;
    overflow: hidden;
    

    button, .tq-ui-switch-buttons-vertical__button {
        border: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 30px;

        white-space: nowrap;
        text-overflow: ellipsis;
        
        outline: 0px;
        overflow: hidden;
        flex-grow: 1;
        border-top: 1px solid #949494;       

        background-color: #dadada;

        &:hover {
            background-color: #ececec;
        }

        &:active {
            background-color: whitesmoke;
            box-shadow: inset 0px 0px 2px 3px white;
        }

        &:disabled {
            cursor: not-allowed;
            box-shadow: inset -1px 1px 6px 0px #767676;
            background-color: #c9c9c9;
        }

        &:first-child {
            border-top: 0px;
            border-top-left-radius: 3px;
            border-top-right-radius: 3px;
        }

        &:last-child {
            border-bottom-left-radius: 3px;
            border-bottom-right-radius: 3px;
        }
    }
}