.tq-ui-toolbar {
    z-index: 200;
    position: absolute;
    left: 7px;
    top: 18px;
    height: calc(100% - 36px);
    display: flex;
    flex-direction: column;

    .tq-ui-switch-buttons-vertical {
        margin-top: 5px;
    }
}

.tq-ui-toolbar__button {
    border: 1px solid darkgray;
    width: 30px;
    height: 30px;
    min-width: 30px;
    min-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #dddddd;
    padding: 0px;
    border-radius: 5px;
    margin-top: 5px;
    box-shadow: 0px 0px 6px -2px black;
    
    outline: 0px;
    overflow: hidden;

    &:first-child {
        margin-top: 0px;
    }

    &:hover {
        box-shadow: 0px 0px 6px -1px black;
        background-color: #ececec;
    }

    &:active {
        box-shadow: 0px 0px 4px -1px black;
    }

    &:disabled {
        cursor: not-allowed;
        box-shadow: initial;
        opacity: 0.5;
    }
}

.tq-ui-toolbar__button.tq-ui-selected {
    background-color: darkgray;
    border: 1px solid gray;
}
