/* Info panel */

.tq-ui-flying-panel {
    min-height: 30px;
    min-width: 30px;
    position: fixed;
    border: 1px solid #c6c6c6;
    background-color: whitesmoke;
    height: 500px;
    width: 500px;
    top: 50px;
    left: 50px;
    z-index: 400;
    opacity: 1;
    transition: opacity 0.5s;
    user-select: none;
    box-shadow: 0px 0px 8px -1px black;
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    .tq-ui-flying-panel__border {
        position: absolute;
        background-color: black;
        opacity: 0;
    }

    .tq-ui-top-border {
        width: 100%;
        height: 3px;
        top: 0;
        cursor: n-resize;
    }
    .tq-ui-right-border {
        height: 100%;
        width: 3px;
        right: 0;
        cursor: e-resize;
    }
    .tq-ui-bottom-border {
        width: 100%;
        height: 3px;
        bottom: 0;
        cursor: s-resize;
    }
    .tq-ui-left-border {
        height: 100%;
        width: 3px;
        left: 0;
        cursor: w-resize;
    }

    .tq-ui-top-left-border {
        width: 4px;
        height: 4px;
        top: 0;
        left: 0;
        cursor: nw-resize;
    }
    .tq-ui-top-right-border {
        width: 4px;
        height: 4px;
        top: 0;
        right: 0;
        cursor: ne-resize;
    }
    .tq-ui-bottom-left-border {
        width: 4px;
        height: 4px;
        bottom: 0;
        left: 0;
        cursor: sw-resize;
    }
    .tq-ui-bottom-right-border {
        width: 4px;
        height: 4px;
        bottom: 0;
        right: 0;
        cursor: se-resize;
    }
}

.tq-ui-flying-panel.tq-ui-collapsed {
    pointer-events: none;
    opacity: 0;
}

.tq-ui-flying-panel_header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0px 7px;
    cursor: all-scroll;
    flex-direction: column;
}

.tq-ui-flying-panel_header__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 6px;
}

.tq-ui-flying-panel__panel {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.tq-ui-body {
    margin: 5px;
    flex-grow: 1;
    position: relative;
}

.tq-ui-body_default-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #fff;
    border-radius: 5px;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    border: .5px solid #dbd8d8;
}

.tq-ui-body_empty-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
}
