/* Info panel */

.tq-ui-info-panel {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 100%;
    height: 100%;
    user-select: none;
    overflow-y: auto;
    overflow-x: hidden;
}

.tq-ui-info-panel_empty-stuff {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.tq-ui-info-panel_related-elements {
    background: whitesmoke;
    flex-grow: 1;
    border: 1px solid #dfdddd;
    padding: 1px 10px;
    overflow-y: auto;
    overflow-x: hidden;
    user-select: initial;
    min-height: 100px;
}

.tq-ui-info-panel_related-elements_list {
    margin-top: 5px;
    padding: 0px;
}
