.tq-ui-colored-buttons-list {
    flex-grow: 1;
    margin-top: 10px;
    padding-top: 10px;
    margin-bottom: 5px;
    padding-bottom: 10px;
}

.tq-ui-colored-button {
    width: 30px;
    height: 30px;
    min-width: 30px;
    min-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 10px;

    span {
        font-weight: bold;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        padding: 4px;
        font-size: 12px;
    }
}