/* Search */
.tq-ui-search-input {
    height: 100%;
    border-radius: 5px;
    border: 1px solid #c5c5c5;
    outline: 0px;
    padding: 2px 5px 2px 22px;
    background: white;
    width: 100%;
}

.tq-ui-search-icon {
    position: absolute;
    top: 9px;
    left: 4px;
    opacity: 0.7;
    cursor: pointer;

    &:hover {
        opacity: 0.9;
    }

    &:active {
        opacity: 1;
    }
}