@import "coloredButtonsList";
@import "flyingPanel";
@import "infoPanel";
@import "legends";
@import "map";
@import "progressScreen";
@import "searchPanel";
@import "switchButtons";
@import "tabPanel";
@import "toolbar";

.tq-ui-group {
    background-color: #ebebeb;
    border-radius: 2px;
    border: 1px solid #cecece;
    margin-bottom: 15px;
    padding: 10px;
    
    .tq-button {
        margin-right: 10px;
    }
}

.tq-label {
    font-size: 14px;
    font-weight: 700;
    color: gray;
    margin-left: 2px;
}

.tq-button {
    box-shadow: inset 0 0 14px 0px white;
    padding: 0px 10px;
    background-color: whitesmoke;
    border-radius: 5px;
    border: solid #b2b2b2;
    border-width: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
    overflow: hidden;
    outline: 0px;

    &:disabled {
        background-color: whitesmoke;
        box-shadow: 0px 0 0 0 white!important;
        cursor: not-allowed;
    }

    &:hover {
        background-color: white;
    }

    &:active {
        background-color: whites;
        box-shadow: inset 0 0 14px -3px #c5c5c5;
    }
}

.tq-ui-hanging-button {
    border: 1px solid darkgray;
    width: 60px;
    height: 60px;
    min-width: 60px;
    min-height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #dddddd;
    padding: 0px;
    border-radius: 5px;
    margin-top: 5px;
    box-shadow: 0px 0px 6px -2px black;
    
    outline: 0px;
    overflow: hidden;

    &:first-child {
        margin-top: 0px;
    }

    &:hover {
        box-shadow: 0px 0px 6px -2px black;
        background-color: #ececec;
    }

    &:active {
        box-shadow: 0px 0px 4px -1px black;
    }

    &:disabled {
        pointer-events: none;
        width: 80px;
        height: 80px;
        min-width: 80px;
        min-height: 80px;
        opacity: 0;
    }
}

.tq-ui-property-button,
.tq-ui-property-button {
    margin-bottom: 5px;
    min-height: 30px;
    background-color: #e7e7e7;
    overflow: hidden;
    padding: 5px;
    white-space: nowrap;
    border-radius: 5px;
    border: 1px solid #dad5d5;
    text-overflow: ellipsis;
    cursor: pointer;
    user-select: none;

    &:hover {
        background-color: #d8d8d8;
    }

    &:active {
        box-shadow: inset 0px 0px 12px -3px gray;
    }
}

.tq-ui-property {
    background: whitesmoke;
    height: 30px;
    min-height: 30px;
    margin-bottom: 10px;
    border-radius: 4px;
    border: 1px solid #dfdddd;
    padding: 1px 10px;
    display: flex;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    user-select: initial;
    text-overflow: ellipsis;
}

.tq-ui-neighbor-gram-icon {
    width: 100%;
    height: 100%;
    padding: 5px;
    content: url('../../src/resources/images/neighbor-gram-icon.png');
    @media screen\0 {
        background: url('../../src/resources/images/neighbor-gram-icon.png') no-repeat;
        background-size: cover;
    }
}

.tq-ui-focus-node-icon {
    width: 100%;
    height: 100%;
    padding: 5px;
    content: url('../../src/resources/images/set-focus-node.png');
    @media screen\0 {
        background: url('../../src/resources/images/set-focus-node.png') no-repeat;
        background-size: cover;
    }
}

.tq-ui-horizontal-collapse-button {
    height: 20px;
    width: 20px;
    content: url('../../src/resources/images/close-horizontal.png');
    @media screen\0 {
        background: url('../../src/resources/images/close-horizontal.png') no-repeat;
        background-size: cover;
    }
    opacity: 0.2;
    cursor: pointer;

    &:hover {
        opacity: 0.3;
    }
    &:active {
        opacity: 0.4;
    }
}

.tq-ui-windows-collapse-button {
    height: 15px;
    width: 15px;
    content: url('../../src/resources/images/close.png');
    @media screen\0 {
        background: url('../../src/resources/images/close.png') no-repeat;
        background-size: cover;
    }
    opacity: 0.2;
    cursor: pointer;

    &:hover {
        opacity: 0.3;
    }
    &:active {
        opacity: 0.4;
    }
}


.tq-ui-window-header-label {
    margin: 0;
    color: #717171;
    font-size: 14px;
}

@supports (-ms-ime-align:auto) {
    .tq-ui-neighbor-gram-icon {
        background: url('../../src/resources/images/neighbor-gram-icon.png') no-repeat;
        background-size: cover;
    }
    
    .tq-ui-focus-node-icon {
        background: url('../../src/resources/images/set-focus-node.png') no-repeat;
        background-size: cover;
    }
    
    .tq-ui-horizontal-collapse-button {
        background: url('../../src/resources/images/close-horizontal.png') no-repeat;
        background-size: cover;
    }
    
    .tq-ui-windows-collapse-button {
        background: url('../../src/resources/images/close.png') no-repeat;
        background-size: cover;
    }
}
