.tq-ui-map.tq-ui-collapse-panel {
    position: absolute;
    top: calc(100% - 62px);
    left: calc(100% - 62px);
    border: 1px solid darkgray;
    background: whitesmoke;
    overflow: hidden;
    border-radius: 10px;
    z-index: 100;
}

.tq-ui-map.tq-ui-collapsed {
    width: 52px;
    height: 52px;
}

.tq-ui-map.tq-ui-expanded {
    width: 200px;
    height: 220px;
    top: calc(100% - 230px);
    left: calc(100% - 210px);
}

.tq-ui-map .tq-ui-collapse-panel_body__label {
    font-size: 30px;
    padding-bottom: 3px;
    padding-left: 1px;
}

.tq-ui-map_map-window {
    width: 200px;
    height: 220px;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 4px;
}

.tq-ui-map_map-window__header {
    position: relative;
    top: 0;
    left: 0;
    height: 20px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0px 5px;
    justify-content: space-between;
    cursor: all-scroll;
}

.tq-ui-map_map-window__map {
    position: relative;
    top: 20;
    left: 0;
    height: calc(100% - 20px);
    width: 100%;
    border-radius: 10px;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    border: 0.5px solid #ededed;
    background: white;
    overflow: hidden;
}