.tq-ui-tab-panel {
    width: 100%;
    height: 100%;
}

.tq-ui-tab-switcher {
    height: 24px;
    flex-grow: 1;
    margin: 0px 5px 10px 5px;
}

.tq-ui-tab-panel_page {
    width: 100%;
    height: calc(100% - 34px);
    background-color: #fff;
    border-radius: 5px;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    border: .5px solid #dbd8d8;
    padding: 10px;
}

.tq-ui-tab-panel_page_body {
    width: 100%;
    height: calc(100% - 5px);
    overflow-x: hidden;
    overflow-y: auto;
}